@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700&display=swap);
body {
  background-color: black;
  font-family: 'Montserrat', sans-serif;
  margin: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.weather-app-wrapper {
    align-items: center;
    display: flex;
    flex-direction: column;
    height: 100vh;
    justify-content: center;
}

@media screen and (max-width: 767px) {
    .weather-app-wrapper {
        height: 100%;
    }
}
.boxes-wrapper {
    position: relative;
    justify-content: center;
    display: flex;
}

.boxes-wrapper .box {
    align-items: center;
    border: 1px solid #ffffff;
    color: #fff;
    display: flex;
    flex-direction: column;
    height: 300px;
    justify-content: center;
    position: relative;
    width: 20%;
}

.day-name {
    font-size: 20px;
    letter-spacing: 1px;
    margin-bottom: 20px;
    position: absolute;
    top: 15px;
}

.weather-rating {
    font-size: 20px;
    letter-spacing: 1px;
    margin-top: 20px;
}

.date {
    bottom: 15px;
    position: absolute;
}

@media screen and (max-width: 767px) {
    .boxes-wrapper {
        flex-direction: column;
        padding-bottom: 20px;
    }
    .boxes-wrapper .box {
      flex-direction: column;  
      width: 100%;
    }
}
.city-info-wrapper {
    align-items: center;
    color: #ffffff;
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
}

.city-info-wrapper .city-name {
    font-size: 40px;
    margin: 0;
}

.city-info-wrapper .date-time {
    font-size: 15px;
    margin-top: 5px;
}
.container-fluid {
    box-sizing: border-box;
    margin: 0 auto;
    padding: 0 15px;
    position: relative;
    width: 100%;
}
.welcome-wrapper {
    align-items: center;
    background-color: #000;
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: center;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1;
}

.welcome-wrapper .request-text {
    color: #fff;
    margin-top: 15px;
}

.welcome-wrapper .wait-text {
    color: #fff;
    font-size: 25px;
    margin-top: 15px;
}
